*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  font: inherit;
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  height: 100%;
  line-height: 1.25;
  text-rendering: optimizeSpeed;
}

img, svg, picture {
  display: block;
  max-width: 100%;
}

h1, h2, h3 {
  line-height: 1;
}
@media only screen and (min-width: 56.95em) {
  h1, h2, h3 {
    line-height: 1.25;
  }
}

ul[role=list],
ol[role=list] {
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

span, i {
  pointer-events: none;
}

button {
  border: 0;
}

:root {
  --ff-heading: Montserrat, sans-serif;
  --ff-primary: corbel, veradna, system-ui;
  --ff-nav: Rubik, sans-serif;
  --fs-300: clamp(0.7956rem, 0.8472rem - 0.075vw, 0.8331rem);
  --fs-400: clamp(1rem, 0.9531rem + 0.25vw, 1.125rem);
  --fs-500: clamp(1.2rem, 1.0535rem + 0.7813vw, 1.5906rem);
  --fs-600: clamp(1.44rem, 1.1365rem + 1.6188vw, 2.2494rem);
  --fs-700: clamp(1.7281rem, 1.1834rem + 2.905vw, 3.1806rem);
  --fs-800: clamp(2.0738rem, 1.1648rem + 4.8475vw, 4.4975rem);
  --fs-900: clamp(2.4881rem, 1.0364rem + 7.7425vw, 6.3594rem);
  --fs-1000: clamp(2.9863rem, 0.7341rem + 12.0113vw, 8.9919rem);
  --fw-400: 400;
  --fw-500: 500;
  --fw-600: 600;
  --fw-700: 700;
  --fw-800: 800;
  --fw-900: 900;
}

body {
  font-size: var(--fs-500);
  font-family: var(--ff-primary);
}

h1, h2, h3 {
  font-family: var(--ff-heading);
}

nav > ul {
  font-family: var(--ff-nav);
}

button {
  cursor: pointer;
}

.heading-1 {
  font-size: var(--fs-900);
  font-weight: var(--fw-900);
}

.heading-2 {
  font-size: var(--fs-700);
  font-weight: var(--fw-800);
}

.heading-3 {
  font-size: var(--fs-600);
  font-weight: var(--fw-700);
}

.brand-logo {
  height: 80px;
  width: 80px;
  overflow: hidden;
  display: grid;
  place-content: center;
}
.brand-logo > * {
  object-fit: cover;
}

.round {
  border-radius: 50%;
  aspect-ratio: 1/1;
}

.wrapper {
  --padding: 3rem;
  --max-width: 75rem;
  width: min(100% - var(--padding) * 2, var(--max-width));
  margin-inline: auto;
}

.section {
  padding-block: 5rem;
  border: 1px dotted;
}

.ff-heading {
  font-size: var(--ff-heading);
}

.ff-primary {
  font-size: var(--ff-primary);
}

.ff-nav {
  font-size: var(--ff-nav);
}

.fs-300 {
  font-size: var(--fs-300);
}

.fs-400 {
  font-size: var(--fs-400);
}

.fs-500 {
  font-size: var(--fs-500);
}

.fs-600 {
  font-size: var(--fs-600);
}

.fs-700 {
  font-size: var(--fs-700);
}

.fs-800 {
  font-size: var(--fs-800);
}

.fs-900 {
  font-size: var(--fs-900);
}

.fs-1000 {
  font-size: var(--fs-1000);
}

.fw-400 {
  font-size: var(--fw-400);
}

.fw-500 {
  font-size: var(--fw-500);
}

.fw-600 {
  font-size: var(--fw-600);
}

.fw-700 {
  font-size: var(--fw-700);
}

.fw-800 {
  font-size: var(--fw-800);
}

.fw-900 {
  font-size: var(--fw-900);
}

.even-columns {
  display: grid;
  gap: 1rem;
}
@media only screen and (min-width: 56.95em) {
  .even-columns {
    grid-auto-flow: column;
    grid-auto-columns: 1fr;
  }
}

.flex-group {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.site-header {
  background: hsl(0, 0%, 78%);
  padding-block: 1rem;
}

.site-header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav a {
  text-decoration: none;
  text-transform: uppercase;
  color: currentColor;
}